export default {
	props:{
		item:{
			type:Array
		}
	},
	data() {
		return {
			info: true,
			isguanZhu:false
		};
	},
	methods:{
		changeGuanZhu(){
			this.isguanZhu = !this.isguanZhu
			if(!this.isguanZhu){
				uni.showToast({
					title:"关注成功"
				})
			}else{
				uni.showToast({
					title:"取消关注"
				})
			}
		}
	}
};
